import React, {useEffect, useState} from 'react';
import { Link } from 'react-router-dom';
function Mainauction(props) {
    const [lst,setList]=useState([]);// 

    useEffect(()=>{
        fetch('/lobby/list') // url http  url   promise 
            .then(res=>res.json())
            .then(res=>{
                setList(res);
            });
    },[]);
    const RoomCreate=(e)=>{ //  
        let name=prompt(' ').trim();

        if(name.length===0)
            return; //  

        //    
        // fetch 

        // 
        fetch('/lobby/create',{
            method:'POST',
            headers:{
                'Content-Type':'application/json'
            },
            body:JSON.stringify({
                name
            })
        })//post option  
            .then(res=>res.json())
            .then(res=>{
                setList([
                    res,
                    ...lst
                ])
            })
    }
    return (
        <div>
            <button onClick={RoomCreate} style={{backgroundColor:'yellow'}}></button>
            <hr/>
            <ul>
                {
                    lst.map((item,idx)=>{
                        return <Link key={idx} to={'/room/'+item.roomId}><li>{idx+1}. {item.roomName}</li></Link>
                        // a 
                    })
                }
            </ul>
        </div>
    );
}
export default Mainauction;